import {
	Button,
	ButtonGroup,
	Card,
	CardBody,
	Divider,
	Flex,
	Heading,
	HStack,
	Image,
	Stack,
	Text,
} from "@chakra-ui/react";
import React from "react";

const ProjectCard = () => {
	return (
		<>
			<Card
				direction={{ base: "column", sm: "row" }}
				overflow="hidden"
				variant="outline"
				background={"black"}
				color="white"
				width="70%"
			>
				<Image
					objectFit="cover"
					maxW={{ base: "100%", sm: "200px" }}
					src="https://user-images.githubusercontent.com/105777295/218666491-07366d7f-43d0-4717-99a1-31f70a571f2c.png"
					alt="project image"
				/>
				<Stack lineHeight={"2rem"}>
					<CardBody>
						<Heading>project name</Heading>
						<Text>
							some text some text some text some textsome text
							some text some text some text some text some text
							some text some text
						</Text>
						<HStack>
							<Text>Tech Stack</Text>
							<Image
								src="https://i.postimg.cc/QNWsW-Bnf/html5-logo-31819.png"
								width={"100px"}
							/>
						</HStack>

						<Divider my={5} />
						<ButtonGroup color={"black"}>
							<Button>Github</Button>
							<Button>Deployed</Button>
						</ButtonGroup>
					</CardBody>
				</Stack>
			</Card>
		</>
	);
};

export default ProjectCard;
